export const AssertAndValidatePasscode = (passcode, dispatch) => {
  if (!validators.checkPasswordValid(passcode)) {
    dispatch({ type: 'APIACCESS_ERROR', error: 'Invalid password!' });
    return 0;
  }
  return 1;
};
